import { FormOrganizationComponent } from './form-organizationUnit/form-organizationUnit.component';
import { OrganizationComponent } from './organizationUnit.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const organizationRoutes: Routes = [
  {path: '', component: OrganizationComponent},
  {path: 'new', component: FormOrganizationComponent},
  {path: 'edit/:id', component: FormOrganizationComponent}
];

/**
 * @description
 * Módulo responsável por gerenciar as rotas do módulo de organization
 */
@NgModule({
  imports: [RouterModule.forChild(organizationRoutes)],
  exports: [RouterModule]
})

/**
 * @description
 * A classe de roteamento do modulo de organization
 */
export class OrganizationRoutingModule { }



