import { HubCrudComponentForm } from './../../utils/hub-crud-component-edit';
import { OrganizationService, OrganizationData } from './../organizationUnit.service';
import { Component, AfterContentChecked } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { HubApiQueryString } from '../../utils/hub-api';
import { TenantService } from '../../tenant/tenant.service';
import { AuthService } from '../../auth/auth.service';
import { TenantThfLookupService } from '../../tenant/tenant.thflookup.service';
import { ThfLookupColumn } from '@totvs/thf-ui/components/thf-field/thf-lookup/interfaces/thf-lookup-column.interface';
import { OrganizationThfLookupService } from '../organizationUnit.thflookup.service';

@Component({
  selector: 'app-form-organization',
  templateUrl: './form-organizationUnit.component.html',
  styleUrls: ['./form-organizationUnit.component.css']
})

/**
 * @description
 * A classe responsável pelas ações do formulário de organization
 * @extends HubCrudComponentForm
 */
export class FormOrganizationComponent extends HubCrudComponentForm<OrganizationData, number> implements AfterContentChecked {
  public parentName: string;
  public tenantColumns: ThfLookupColumn[] = [
    { column: 'tenantName', label: 'Nome', type: 'string' },
    { column: 'name', label: 'Nome de Exibição', type: 'string', fieldLabel: true}
  ];
  public organizationColumns: ThfLookupColumn[] = [
    { column: 'name', label: 'Nome', type: 'string' },
    { column: 'displayName', label: 'Nome de Exibição', type: 'string', fieldLabel: true }
  ];

  private tenantId;

  /**
   * O Contrutor para inicialização
   */
  constructor(protected activatedRoute: ActivatedRoute,
    public router: Router,
    public dataService: OrganizationService,
    public tenantThfLookupService: TenantThfLookupService,
    public organizationThfLookupService: OrganizationThfLookupService,
    public tenantService: TenantService,
    public authService: AuthService) {
    super(activatedRoute, router, dataService, OrganizationData);

    this.beforeEditing.subscribe(data => {
      this.tenantId = data.tenantId;

      if (!authService.isHostUser()) {
        tenantService.getQuery(new HubApiQueryString({
          page: 1,
          pageSize: 1
        })).subscribe((success) => {
          if (success && success.items)
            this.data.tenantId = success.items[0].id;
        });
      }

      if (this.isUpdate) {
        dataService.get(data.parentId)
          .subscribe((success) => {
            if (success)
              this.parentName = success.displayName;
          });
      }
      
      this.onChangedTenant();
    });
  }

  ngAfterContentChecked() {
    if (this.data.tenantId &&
        this.tenantId !== this.data.tenantId) {
      this.tenantId = this.data.tenantId;
      this.onChangedTenant();
    }
  }

  onChangedTenant() {
    this.data.parentId = 0;
    this.organizationThfLookupService.tenantId = this.data.tenantId;
  }
}



