import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';

import { ThfLookupFilter } from '@totvs/thf-ui/components/thf-field';
import { OrganizationService } from './organizationUnit.service';
import { HubApiQueryString } from '../utils/hub-api';

@Injectable()
export class OrganizationThfLookupService implements ThfLookupFilter {
    public tenantId: number;

    constructor(private organizationService: OrganizationService) { }

    getFilteredData(filter: string, page: number, pageSize: number): Observable<any> {
        return this.organizationService.getQueryByTenant(new HubApiQueryString({
            page,
            pageSize,
            filter: [{ key: "name", value: filter }]
        }), this.tenantId);
    }

    getObjectByValue(value: string): Observable<any> {
        return this.organizationService.get(value);
    }
}


