import { OrganizationRoutingModule } from './organizationUnit-routing.module';
import { ThfModule } from '@totvs/thf-ui/thf.module';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { OrganizationComponent } from './organizationUnit.component';
import { FormOrganizationComponent } from './form-organizationUnit/form-organizationUnit.component';
import { OrganizationService } from './organizationUnit.service';
import { AuthService } from '../auth/auth.service';
import { TokenInterceptor } from '../auth/token.interceptor';
import { TenantService } from '../tenant/tenant.service';
import { TenantThfLookupService } from '../tenant/tenant.thflookup.service';
import { OrganizationThfLookupService } from './organizationUnit.thflookup.service';

@NgModule({
  imports: [
    CommonModule,
    ThfModule,
    OrganizationRoutingModule,
    FormsModule,
    HttpClientModule
  ],
  declarations: [
    OrganizationComponent,
    FormOrganizationComponent
  ],
  providers: [
    OrganizationService,
    OrganizationThfLookupService,
    AuthService,
    TenantService,
    TenantThfLookupService,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: TokenInterceptor,
      multi: true
    }
  ]
})

/**
 * @description
 * Classe principal do modulo de organization
 */
export class OrganizationModule { }



